.hero{
    height: 100vh;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
    display: flex;
}
.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
     align-items: flex-start;
    gap: 20px;
    padding-left:180px;
    text-align: left;
    line-height: 1.1;
}

.hero-left h2{
    color: #090909;
    font-size: 28px;
    font-weight: 600;

}
.hero-left p{
    
    color: #171717;
    font-size: 90px;
    font-weight: 600;
}
.hero-hand-icon{
     padding-left:5px;
    display: flex;
    align-items: center;
    gap:20px;
}
.hero-hand-icon img{
    
    width:80px;
}
.hero-latest-btn{
     
    display: flex;
    justify-content: center;
    align-items: center;
    gap:15px ;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
}

.hero-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.hero-right img{
    width: 450px;
     /* padding-right:100px; */
     /* padding-top:60px; */
}
@media(max-width:1280px){
    .hero-left{
        padding-left: 100px;
        text-align: left;
    }
    .hero-left h2{
        font-size: 22px;
        
    }
    .hero-left p{
        font-size: 60px;
    }
    .hero-hand-icon img{
        width: 80px;
    }
    .hero-latest-btn{
        gap: 10px;
        width: 250px;
        height: 60px;
        margin-top: 20px;
        font-size: 18px;
    }
    .hero-right img{
        width: 420px;
    }
}
@media(max-width:1024px){
    .hero-left{
        padding-left: 80px;
    }
    .hero-left h2{
        font-size: 20px;
        
    }
    .hero-left p{
        font-size: 50px;
    }
    .hero-hand-icon img{
        width: 65px;
    }
    .hero-latest-btn{
        width: 220px;
        height: 50px;
        font-size: 16px;
    }
    .hero-right img{
        width: 360px;
    }
}
@media(max-width:800px){
    .hero-left{
        padding-left: 30px;
    }
    .hero-left h2{
        font-size: 16px;
        
    }
    .hero-left p{
        font-size: 40px;
    }
    .hero-hand-icon img{
        width: 50px;
    }
    .hero-latest-btn{
        width: 175px;
        height: 40px;
        font-size: 13px;
    }
    .hero-right img{
        width: 300px;
    }
}
@media(max-width:500px)
{
    .hero{
        flex-direction: column;
    }
    .hero-right{
        display: none;
    }
    .hero-left h2{
        font-size: 18px;
    }
    .hero-left p{
        font-size: 50px;
    }
    .hero-hand-icon img{
        width: 55px;
    }
    .hero-latest-btn{
        width: 200px;
        height: 45px;
        font-size: 14px;
    }
}